'use client';
import React, { useState } from 'react';
import { Square, CheckSquare } from 'lucide-react';

const initialAssignments = [
  {
    id: 1,
    name: "Identify stakeholders who will be impacted by the project and who will be involved.",
    createdBy: "Eleanor Peno",
    dueDate: "2024-10-02",
    projectName: "Neptune CRM",
    isCompleted: false,
  },
  {
    id: 2,
    name: "Develop report for the requirements of the project.",
    createdBy: "Eleanor Peno",
    dueDate: "2024-10-03",
    projectName: "Project Atlas",
    isCompleted: false,
  },
  {
    id: 3,
    name: "Develop project charter by documenting the project's purpose, objectives, and key players.",
    createdBy: "Eleanor Peno",
    dueDate: "2024-11-02",
    projectName: "Project Atlas",
    isCompleted: false,
  },
];

export default function MyAssignment() {
  const [assignments, setAssignments] = useState(initialAssignments);

  const handleCheckboxChange = (id: number) => {
    setAssignments(assignments.map(assignment =>
      assignment.id === id ? { ...assignment, isCompleted: !assignment.isCompleted } : assignment
    ));
  };

  return (
    <div className="h-screen bg-white p-6">
      <h1 className="text-2xl font-normal mb-4">My Assignment</h1>
      <ul className="space-y-4">
        {assignments.map((assignment) => (
          <li
            key={assignment.id}
            className="p-4 bg-gray-50 rounded shadow-sm flex items-center"
          >
            <button 
              onClick={() => handleCheckboxChange(assignment.id)}
              className="focus:outline-none focus:ring-2 focus:ring-blue-500 rounded mr-2"
            >
              {assignment.isCompleted ? (
                <CheckSquare className="w-5 h-5 text-gray-500" />
              ) : (
                <Square className="w-5 h-5 text-gray-400" />
              )}
            </button>
            <div className="flex-1">
              <p className="text-medium text-gray-700">{assignment.name}</p>
              <p className="text-sm text-gray-600">
                {assignment.createdBy} | Due: {assignment.dueDate} | Project: {assignment.projectName}
              </p>
            </div>
          </li>
        ))}
      </ul>
      <button className="mt-6 px-4 py-2 text-[#5D56BD] font-normal rounded">
        Show My Completed Assignment
      </button>
    </div>
  );
}